// 
import { ReactNode, useContext, useEffect, useState } from "react";
import { MenuContext } from '../DropdownMenu';

export const DropdownItem = ({
  children, 
  leftIcon, 
  rightIcon, 
  goToMenu
}: {
  children?: ReactNode,
  leftIcon?: ReactNode,
  rightIcon?: ReactNode, 
  goToMenu?: number
}) => {
  const { activeId, setActiveId, setPreviousId } = useContext(MenuContext);
  
  return (
    <div className="menu-item" onClick={() => goToMenu !== undefined && (setPreviousId(activeId), setActiveId(goToMenu))}>
      <div className="menu-icon-button">{leftIcon}</div>
      {children}
      <div className="menu-icon-button menu-icon-right">{rightIcon}</div>
    </div>
  );
};