// Assets
import { ReactComponent as ArrowIcon } from "assets/icons/arrow.svg";

// 
import { useContext } from "react";
import { MenuContext } from '../DropdownMenu';




export const MenuTitleItem = ({
  goToMenu,
  title
}: {
  goToMenu: number
  title: string
}) => {
  const { activeId, setActiveId, setPreviousId } = useContext(MenuContext);
  
  return (
    <div className="menu-item menu-title-item">
      <div className="menu-icon-button" onClick={() => goToMenu !== undefined && (setPreviousId(activeId), setActiveId(goToMenu))}> 
        <ArrowIcon />
      </div>
      <span>{title}</span>
    </div>    
  );
};