// React
import { useState, useRef } from "react";
import { NavLink } from "react-router-dom";

// 
import { useClickOutside } from 'hooks';



export const FindFriendsNavItem = () => {
  const [isOpen, setIsOpen] = useState(false);
  const navItemRef = useRef(null);

  useClickOutside(navItemRef, () => {
    setIsOpen(false);
  });
  

  return (
    <li className="nav-item hide-nav-item-at-1300" ref={navItemRef}>
      <NavLink
        to="/friends"
        className={({ isActive }) => `icon-button ${isActive ? 'icon-button-highlighted' : ''}`}
        onClick={() => setIsOpen(!isOpen)} >

          <span style={{"paddingBottom": "1px"}}>
              Find Friends
          </span>

      </NavLink>
    </li>
  );
}