// CSS
import './CommentsSection.css';

// Assets
import DefaultUserProfilePicture from "assets/profile-picture.png";



export const Comment = ({
  username,
  message,
  date,
  userIconSrc = undefined,
  wasEdited = false,
} : {
  username: string,
  message: string,
  date: string,
  userIconSrc?: string,
  wasEdited?: boolean,
}) => {
  return (
    <div className='comment-container'>
      <div className='comment-user-icon'>
        <img src={userIconSrc ? userIconSrc : DefaultUserProfilePicture} />
      </div>
      <div className='comment-body'>
        <div className='comment-message'>
          <div className='comment-user-name'>
            { username }
          </div>
          <div className='comment-text'>
            { message }
          </div>
        </div>
        <div className='comment-reaction-container'>
          <div className='comment-reaction-button'>
            Like
          </div>
          <div className='comment-reaction-button'>
            Reply
          </div>
          <div className='comment-reaction-button'>
            Share
          </div>
          <div className='comment-reaction-info'>
            { date }
          </div>
          <div className='comment-reaction-info'>
            { wasEdited && 'Edited'}
          </div>
        </div>
      </div>
    </div>
  );
};


export const CommentsSection = ({
  comments
} : { 
  comments: { 
    username: string,
    message: string,
    date: string,
    userIconSrc?: string,
    wasEdited?: boolean,
  }[]
}) => {
  return (
    <div className='comments-section-container'>
        { comments.map(({username, message, date, userIconSrc, wasEdited}:{username:string, message:string, date:string, userIconSrc?: string, wasEdited?:boolean}) => (
          <Comment 
            key={`${username}-${date}-${wasEdited}`}
            username={username}
            message={message}
            date={date}
            userIconSrc={userIconSrc}
            wasEdited={wasEdited} />
        )) }
    </div>
  );
};