// React
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

// Components
import { NavbarWrapper } from './Navbar/NavbarWrapper';
import { HomeTab, FriendsTab, ProfileTab, GroupsFeedTab, ErrorPage404, ContentPlaceHolder } from './Pages';

// Assets
import BirthdayCake from 'assets/birthday-cake.png';
import TradingImage from 'assets/trading.png'; // FIX: it is here only so that it is included into build.



function App() {
  return (
    // basename='facebook-ui-clone/'
    <BrowserRouter basename='facebook-ui-clone/'> 
        <Routes>
          <Route path="/" element={<NavbarWrapper />}> 
            <Route index element={<HomeTab />} />
            <Route path="friends/" element={<FriendsTab />} >
              <Route index element={<ContentPlaceHolder title="When you have friend requests or suggestions, you'll see them here."/>} />
              <Route path="requests/" element={<ContentPlaceHolder title="When you have friend requests or suggestions, you'll see them here." />} />
              <Route path="suggestions/" element={<ContentPlaceHolder title="When you have friend requests or suggestions, you'll see them here." />} />
              <Route path="list/" element={<ContentPlaceHolder title="No friends to show" description="When you become friends with people on Facebook, they'll appear here." />} />
              <Route path="birthdays/" element={<ContentPlaceHolder title="When your friends have birthdays, they will appear here." iconSrc={BirthdayCake} />} />
              <Route path="friendlist/" element={<ContentPlaceHolder title="No Custom List to Show" description="When you create a custom list on Facebook, they'll appear here." />} />
            </Route>
            {/* <Route path="profile/" element={<ProfileTab />} /> */}
            <Route path="groups/feed/" element={<GroupsFeedTab />} />
          </Route>
          {/* <Route path="*" element={<ErrorPage404 />} /> */}
          <Route path="*" element={<Navigate replace to="/" />} />
        </Routes>
      </BrowserRouter>
  );
}




export default App;