// 
import { ReactNode, useEffect, useState } from "react";



export const OptionsItem = ({
  leftIcon,
  optionId,
  title,
  description,
  options,
  defaultId = 0
}: {
  leftIcon?: ReactNode,
  optionId: string,
  title: string,
  description: string,
  options: [string, ...string[]],
  defaultId?: number
}) => {
  const [option, setOption] = useState("");

  useEffect(() => {
    let storedOption = localStorage.getItem(`option-${optionId}`);
    storedOption === null ? setOption(options[defaultId]) : setOption(storedOption);
  }, []);

  const OptionField = (optionTitle:string) => (
    <label key={`${optionId}-${optionTitle}`} className="options-item-option" htmlFor={`${optionId}-${optionTitle}`}> 

      {optionTitle}

      <input type="radio" name={optionId} id={`${optionId}-${optionTitle}`}
        checked={option === optionTitle}
        onChange={() => {
          localStorage.setItem(`option-${optionId}`, optionTitle); 
          setOption(optionTitle);
        }} />

    </label>
  );

  return (
      <div className="options-item">
        <div className="menu-icon-button"> {leftIcon} </div>
        
        <div className="options-item-info">
          <div className="options-item-title"> { title } </div>
          <div className="options-item-description"> { description } </div>
          <div className="options-item-option-container">
            { options.map(OptionField) }
          </div>
        </div>
      </div>
  );
};