// Assets
import UserProfilePicture from "assets/profile-picture.png";

// React 
import { useState, useRef } from "react";
import { Link } from "react-router-dom";

//
import { useClickOutside } from 'hooks';




export const MiniProfileNavItem = () => {
  const [isOpen, setIsOpen] = useState(false);
  const navItemRef = useRef(null);

  useClickOutside(navItemRef, () => {
    setIsOpen(false);
  });
  

  return (
    <li className="nav-item hide-nav-item-at-500" ref={navItemRef}>
      <Link to="profile/" className="mini-profile" onClick={() => setIsOpen(!isOpen)}>
        <img src={UserProfilePicture}/>
        <span>Name</span>
      </Link>
    </li>
  );
}
