// 
import { useState, ReactNode, useRef } from "react";
import { CSSTransition } from "react-transition-group";
import { useClickOutside } from 'hooks';



export const NavItem = ({ icon, children, hintMessage = '' }: {icon:ReactNode, children?: ReactNode, hintMessage?: string}) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const navItemRef = useRef(null);

  useClickOutside(navItemRef, () => {
    setIsOpen(false);
  });
  

  return (
    <li className="nav-item" ref={navItemRef}>
      <div className={`icon-button ${isOpen ? 'icon-button-active' : ''}`} onClick={() => (setIsOpen(!isOpen), setIsHovered(false))}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}>
        {icon}
      </div>

      { children &&
        <CSSTransition
          in={isOpen}
          timeout={150}
          classNames="nav-item-content"
          unmountOnExit>
            { children }
        </CSSTransition>
      }

      { hintMessage !== '' && <div className={`nav-item-hint ${isHovered ? 'nav-item-hint-show' : ''}`}>{hintMessage}</div> }
    </li>
  );
}