// CSS
import './MediaContent.css';

// TODO: redesign 'post API' and add proper interface instead of any type
const getMediaContent = (media:any) => {
  switch(media.type) {
    case "image":
      return <img className='media-content-img' src={media.url} />
    case "link": 
      return (
        <a href={media.url} className='media-content-link'>
          <img className='media-content-link-preview' src={media.previewUrl} />
          <div className='media-content-link-info'>
            { media.domainTitle && <div className='media-content-link-domain-title'> { media.domainTitle } </div> }
            { media.title && <div className='media-content-link-title'> { media.title } </div> }
            { media.description && <div className='media-content-link-description'> { media.description } </div> }
          </div>
        </a>
      )
  }
};

// TODO: redesign 'post API' and add proper interface instead of any type
export const MediaContent = ({ media }:{ media:any }) => {
  return (
    <div className='media-content-container'>
        { getMediaContent(media) }
    </div>
  );
};