// React
import { ReactNode, useState } from 'react';

// Components
import {
  CommentsSection,
  ContentOrigin,
  ContentReactions,
  HashTag,
  MediaContent,
  TextContent
} from './Blocks';

// CSS
import './Post.css';



const ContentContainer = ({ children }:{ children: ReactNode }) => (
  <div className='content-container'>
    { children }
  </div>
);


// TODO: redesign 'post API' and add proper interface instead of any type
export const Post = ({ data } : { data:any }) => {
  const [commentVisibility, setCommentVisibility] = useState(false);

  const { origin, media, paragraphs, comments, reactionsCount, sharesCount } = data;

  return (
    <ContentContainer>
      <ContentOrigin origin={origin} />
      <TextContent paragraphs={paragraphs} />

      { media && <MediaContent media={media} /> }

      <ContentReactions
        reactionsCount={reactionsCount}
        commentsCount={comments.length}
        sharesCount={sharesCount}
        commentVisibilityState={[commentVisibility, setCommentVisibility]} />
      
      {commentVisibility && <CommentsSection comments={comments} />}

    </ContentContainer>
  );
};
