// Assets
import { ReactComponent as LikeFilledIcon } from "assets/icons/like-filled.svg";
import { ReactComponent as LikeIcon } from "assets/icons/like.svg";
import { ReactComponent as CommentIcon } from "assets/icons/comment.svg";
import { ReactComponent as ShareIcon } from "assets/icons/share.svg";

// CSS
import './ContentReactions.css';

// Components
import { LineDivider } from 'components/UI';




export const ContentReactions = ({
  reactionsCount,
  commentsCount,
  sharesCount,
  commentVisibilityState
}: { 
  reactionsCount: number,
  commentsCount: number,
  sharesCount: number,
  commentVisibilityState: [boolean, Function]
}) => {
  const [commentVisibility, setCommentVisibility] = commentVisibilityState;

  return (
    <>
      <div className='content-reactions-container'>
        <div className='content-reactions-icon'>
          <LikeFilledIcon />
        </div>
        <div className='content-reactions-number'>
          { reactionsCount && `${reactionsCount}` }
        </div>
        <div className='content-reactions-comments-number' onClick={() => {setCommentVisibility(!commentVisibility)}}>
          { commentsCount > 1 && `${commentsCount} Comments` }
        </div>
        <div className='content-reactions-shares-number'>
          { sharesCount && `${sharesCount}` }
        </div>
      </div>
      <LineDivider />
      <div className='content-reactions-menu'>
        <div className='content-reactions-button'>
            <LikeIcon /> &thinsp; Like
        </div>
        <div className='content-reactions-button' onClick={() => {setCommentVisibility(!commentVisibility)}}>
            <CommentIcon /> &thinsp; Comment
        </div>
        <div className='content-reactions-button'>
            <ShareIcon /> &thinsp; Share
        </div>
      </div>

      {commentVisibility && <LineDivider />}
    </>
  );
};