// Assets
import { ReactComponent as ChevronIcon } from "assets/icons/chevron.svg";
import { ReactComponent as CogIcon } from "assets/icons/cog.svg";
import { ReactComponent as SearchIcon } from "assets/icons/search.svg";
import { BirthdaysIcon, FriendRequestsIcon, FriendsListIcon, HomeIcon, SuggestionsIcon } from './Icons';

// 
import { useEffect, useRef, useState } from "react";
import { Outlet, useLocation } from "react-router-dom";

// CSS
import './Friends.css';

// Components
import { MenuContext, FriendsMenuItem, FriendsMenuPage, MenuPageTitle } from './FriendsMenu';
import { LineDivider } from 'components/UI';



const MenuTab = () => {
  const [previousId, setPreviousId] = useState(-1);
  let [activeId, setActiveId] = useState(0);
  const dropdownRef: any = useRef(null);
  const loc = useLocation();


  useEffect(() => {
    let target = 0;
    
    if (loc.pathname.includes('requests')) target = 1;
    if (loc.pathname.includes('suggestions')) target = 2;
    if (loc.pathname.includes('list')) target = 3;
    if (loc.pathname.includes('birthdays')) target = 0;
    if (loc.pathname.includes('friendlist')) target = 4;

    if (target !== activeId){
      setPreviousId(activeId);
      setActiveId(target);
    }
  }, [loc]);

  return (
    <div className="friends-tab-menu" ref={dropdownRef}>
      <MenuContext.Provider value={{
          activeId: activeId,
          previousId: previousId,
          setPreviousId: setPreviousId,
          setActiveId: setActiveId
        }}>

        <FriendsMenuPage id={0}>
          <div className="friends-tab-menu-page">
            <div className="friends-tab-menu-info">
              <div className="friends-tab-menu-title">
                Friends
              </div>
              <div className="friends-tab-menu-settings">
                <CogIcon />
              </div>
            </div>

            <FriendsMenuItem
              linkTo="/friends"
              leftIcon={<HomeIcon />}
              goToMenu={0}>
              <span> Home </span>
            </FriendsMenuItem>

            <FriendsMenuItem
              linkTo="/friends/requests"
              leftIcon={<FriendRequestsIcon />}
              rightIcon={<ChevronIcon />}
              goToMenu={1}>
              <span> Friend Requests </span>
            </FriendsMenuItem>

            <FriendsMenuItem
              linkTo="/friends/suggestions"
              leftIcon={<SuggestionsIcon />}
              rightIcon={<ChevronIcon />}
              goToMenu={2}>
              <span> Suggestions </span>
            </FriendsMenuItem>

            <FriendsMenuItem
              linkTo="/friends/list"
              leftIcon={<FriendsListIcon />}
              rightIcon={<ChevronIcon />}
              goToMenu={3}>
              <span> All friends </span>
            </FriendsMenuItem>

            <FriendsMenuItem
              linkTo="/friends/birthdays"
              leftIcon={<BirthdaysIcon />}
              goToMenu={0}>
              <span> Birthdays </span>
            </FriendsMenuItem>

            <FriendsMenuItem
              linkTo="/friends/friendlist"
              leftIcon={<FriendsListIcon />}
              rightIcon={<ChevronIcon />}
              goToMenu={4}>
              <span> Custom Lists </span>
            </FriendsMenuItem>

          </div>
        </FriendsMenuPage>

        <FriendsMenuPage id={1}>
          <div className="friends-tab-menu-page">
            <MenuPageTitle title="Friend Requests" linkTo="/friends" />
            <LineDivider />
            
          </div>
        </FriendsMenuPage>

        <FriendsMenuPage id={2}>
          <div className="friends-tab-menu-page">
            <MenuPageTitle title="Suggestions" linkTo="/friends" />
          </div>
        </FriendsMenuPage>

        <FriendsMenuPage id={3}>
          <div className="friends-tab-menu-page">
            <MenuPageTitle title="All friends" linkTo="/friends" />
            <div className={'friends-tab-menu-search'}>
              <span className='friends-tab-menu-search-icon'><SearchIcon /></span>
              <input className="friends-tab-menu-search-input" placeholder='Search Friends' />
            </div>
            <LineDivider />
            <div style={{marginTop:'8px', marginLeft: '16px'}}>
              Friends
            </div>
            <div style={{marginTop:'4px', marginLeft: '16px', fontSize: '.8125rem', fontWeight: '400', color: 'var(--secondary-text)'}}>
              No friends to show
            </div>
          </div>
        </FriendsMenuPage>

        <FriendsMenuPage id={4}>
          <div className="friends-tab-menu-page">
            <MenuPageTitle title="Custom Lists" linkTo="/friends" />
            
          </div>
        </FriendsMenuPage>


      </MenuContext.Provider>
    </div>
  );
};




export const FriendsTab = () => {
  return (
    <div className='friends-tab-container'>
      <MenuTab />
      <Outlet />
    </div>
  );
}
