// Assets
import { ReactComponent as ArrowIcon } from "assets/icons/arrow.svg";

// 
import { createContext, ReactNode, useContext } from "react";
import { Link, NavLink } from "react-router-dom";
import { CSSTransition } from 'react-transition-group';



export const MenuContext = createContext({
  activeId: 0, 
  previousId: -1,
  setPreviousId: (n: number) => {}, 
  setActiveId: (n: number) => {}
});



export const FriendsMenuPage = ({
  children,
  id
}: {
  children?: ReactNode,
  id: number
}) => {
  const { activeId, previousId } = useContext(MenuContext);

  return (
    <CSSTransition
      in={activeId === id}
      timeout={150}
      classNames={`friends-tab-menu-slide-${(activeId <= id && previousId <= id) ? 'right' : 'left'}`}
      unmountOnExit >
      
      { children }

    </CSSTransition>
  );
};



export const FriendsMenuItem = ({
  linkTo,
  children,
  leftIcon, 
  rightIcon, 
  goToMenu
} : {
  linkTo: string,
  children?: ReactNode,
  leftIcon?: ReactNode,
  rightIcon?: ReactNode, 
  goToMenu?: number
}) => {
  const { activeId, setActiveId, setPreviousId } = useContext(MenuContext);
  
  return (
    <NavLink end to={linkTo} className={({ isActive }) => `friends-tab-menu-item ${!rightIcon && isActive ? 'friends-tab-menu-item-activated' : ''}`} onClick={() => goToMenu !== undefined && (setPreviousId(activeId), setActiveId(goToMenu))}>
      <div className="friends-tab-menu-icon-button">{leftIcon}</div>
      { children }
      <div className="friends-tab-menu-icon-button friends-tab-menu-icon-right">{rightIcon}</div>
    </NavLink>
  );
};



export const MenuPageTitle = ({ title, linkTo } : { title: string, linkTo:string }) => {
  return (
    <div className="friends-tab-menu-page-navigation">
      <Link to={linkTo}>
        <ArrowIcon />
      </Link>
      <div className="friends-tab-menu-page-title-container">
        <Link to={linkTo} className="friends-tab-menu-page-link">Friends</Link>
        <div className="friends-tab-menu-page-title">{ title }</div>
      </div>
    </div>
  );
};


