// Assets
import { ReactComponent as FacebookIcon } from "assets/icons/facebook_icon.svg";
import { ReactComponent as SearchIcon } from "assets/icons/search.svg";

// Hooks
import { useRef, useState } from "react";
import { Link } from "react-router-dom";

// CSS
import './SearchMenu.css';



export const SearchMenu = () => {
  const [inputFocused, setInputFocused] = useState(false);
  const divRef = useRef(null);
  
  return (
    <div className="search-menu">
      <Link to="/" className={`search-menu-fb-icon ${inputFocused ? 'search-menu-fb-icon-focused' : ''}`}> 
        <FacebookIcon />
      </Link>
      
      <div ref={divRef} className={`search-menu-focused-container ${inputFocused ? 'search-menu-focused-container-focused' : ''}`} />
      
      <div className={`search-menu-search ${inputFocused ? 'search-menu-search-focused' : ''}`}>
        <span className={`search-menu-icon ${inputFocused ? 'search-menu-icon-focused' : ''}`}><SearchIcon /></span>
        <input className="search-menu-input" placeholder={`${inputFocused ? '' : ' '}Search Facebook`} 
               onFocus={() => setInputFocused(true)}
               onBlur={() => setInputFocused(false)} />
      </div>
    </div>
  );
};