// Components
import { DropdownDisplayPage, DropdownHelpPage, DropdownHomePage, DropdownKeyboardPage, DropdownSettingsPage } from './DropdownMenuPages';

// 
import { useState, useEffect, useRef, createContext } from "react";

// CSS
import "./DropdownMenu.css";



export const MenuContext = createContext({
  activeId: 0, 
  previousId: -1, 
  calcHeight: (el: HTMLElement) => {}, 
  setPreviousId: (n: number) => {}, 
  setActiveId: (n: number) => {}
});



export const DropdownMenu = () => {
  const [previousId, setPreviousId] = useState(-1);
  const [activeId, setActiveId] = useState(0);
  const [menuHeight, setMenuHeight] = useState(0);
  const dropdownRef: any = useRef(null);

  useEffect(() => {
    setMenuHeight(dropdownRef.current?.firstChild?.offsetHeight);
  }, []);

  function calcHeight(el: HTMLElement) {
    const height = el.offsetHeight;
    setMenuHeight(height);
  }


  return (
    <div className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>
      <MenuContext.Provider value={{
          activeId: activeId,
          previousId: previousId,
          calcHeight: calcHeight,
          setPreviousId: setPreviousId,
          setActiveId: setActiveId
        }}>

        <DropdownHomePage />
        <DropdownSettingsPage />
        <DropdownHelpPage />
        <DropdownDisplayPage />
        <DropdownKeyboardPage />

      </MenuContext.Provider>
    </div>
  );
}