const GroupLink = ({
  logoURL,
  name,
  post_for_you = 0
} : {
  logoURL:string,
  name:string,
  post_for_you?:number
}) => {
  return (
    <div className="group-link">
      <img src={logoURL} />
      <div className="info">
        <div className="group-name"> { name } </div>
        <div className="additional-info"> 
          { post_for_you 
            ? <>
                <span style={{width: '8px', height:'8px', background: 'var(--accent)', borderRadius: '50%', display:"inline-flex", marginBottom: '0.03rem', marginRight: '4px'}}></span>
                <span>{post_for_you} {post_for_you == 1 ? 'post' : 'posts'} for you</span>
              </> 
            : "Last active 2 days ago" } 
        </div>
      </div>
    </div>
  );
};



const Groups = () => {
  return (
    <div className="groups-menu">
      <div className="title-block">
        <div className="title"> Groups </div>
        <MenuButton icon={<CogIcon />}/>
      </div>
      
      <div className="search-container">
        <SearchBar placeholder="Search groups" icon />
      </div>
      
      <div className="navigation-block">
        <NavigationButton iconSrc={NewsFeedIcon} title="Your feed" />
        <NavigationButton iconSrc={CompassIcon} title="Discover" />
      </div>

      <div className="create-new-group-button">
        Create new group
      </div>

      <LineDivider />

      <div style={{marginLeft: '16px', marginTop: '14px', marginBottom: '8px', fontSize: '1.0625rem', lineHeight: '1.1765', fontWeight: '600'}}> Groups you've joined </div>

      <GroupLink post_for_you={1} name="Deno" logoURL="https://deno.land/logo.svg?__frsh_c=8qaybgjxxdv0" />
      <GroupLink name="NodeJS" logoURL="https://cdn-icons-png.flaticon.com/512/5968/5968322.png" />
      <GroupLink post_for_you={5} name="React" logoURL={ReactIcon} />

    </div>
  );
};



