// React
import { ReactNode } from 'react';

// CSS
import './TextContent.css';

// Components
import { HashTag } from './HashTag';


// TODO: redesign 'post API' and add proper interface instead of any type
const SingleParagraph = (item:any, i2: number) => (
  item.type === "text" ? item.data : <HashTag key={`${i2}-${item.data}`} title={item.data} />
);


export const TextContent = ({ paragraphs }:{ paragraphs: ReactNode[] }) => {
  
  return (
    <div className='text-content-container'>
      {
        // TODO: redesign 'post API' and add proper interface instead of any type
        paragraphs.map(( paragraph:any, i1:number ) => (
          <div key={i1} className='text-content-paragraph'>
            { paragraph.map(SingleParagraph) }
          </div>
        ))
      }
    </div>
  );
};