// Assets
import { ReactComponent as HomeIcon } from "assets/icons/home_filled.svg";
import { ReactComponent as FriendsIcon } from "assets/icons/friends_filled.svg";
import { ReactComponent as GroupsIcon } from "assets/icons/groups_filled.svg";
import { ReactComponent as UnfilledHomeIcon } from "assets/icons/home_unfilled.svg";
import { ReactComponent as UnfilledFriendsIcon } from "assets/icons/friends_unfilled.svg";
import { ReactComponent as UnfilledGroupsIcon } from "assets/icons/groups_unfilled.svg";

// React
import { ReactNode, useState } from "react";
import { NavLink } from "react-router-dom";

// CSS
import './NavigationMenu.css';



const NavigationTab = ({
  hintMessage,
  href,
  iconOff,
  iconOn,
} : {
  hintMessage:string,
  href:string,
  iconOff: ReactNode,
  iconOn: ReactNode,
}) => {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <NavLink 
      to={href}
      className={({ isActive }) => `navigation-menu-button ${isActive ? 'navigation-menu-button-highlighted' : ''}`}
      onClick={() => setIsHovered(false)}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      children = { 
        ({ isActive }) => (
          <>
            { isActive ? iconOn : iconOff }
    
            <div className={`navigation-menu-highlight-off ${isActive ? 'navigation-menu-highlight-on' : ''}`}></div>
            <div className={`navigation-menu-hint ${isHovered ? 'navigation-menu-hint-show' : ''}`}>{hintMessage}</div>
          </>
        )
      } />
  );
}



export const NavigationMenu = () => {
  
  return (
    <div className="navigation-menu">
      
      <NavigationTab 
        hintMessage="Home"
        href='/' 
        iconOff={<UnfilledHomeIcon />} iconOn={<HomeIcon />} />

      <NavigationTab 
        hintMessage="Friends"
        href='/friends'
        iconOff={<UnfilledFriendsIcon />} iconOn={<FriendsIcon />} />

      <NavigationTab 
        hintMessage="Groups"
        href='/groups/feed'
        iconOff={<UnfilledGroupsIcon />} iconOn={<GroupsIcon />} />

    </div>
  );
};