import { MenuContext } from '../../DropdownMenu';

//
import { ReactNode, useContext } from "react";
import { CSSTransition } from 'react-transition-group';

// CSS
import './DropdownMenuPage.css';



export const DropdownMenuPage = ({
  children,
  id
}: {
  children?: ReactNode,
  id: number
}) =>  {
  const { activeId, previousId, calcHeight } = useContext(MenuContext);

  return (
    <CSSTransition
      in={activeId === id}
      timeout={150}
      classNames={`slide-${(activeId <= id && previousId <= id) ? 'right' : 'left'}`}
      unmountOnExit
      onEnter={calcHeight}>
      
      { children }

    </CSSTransition>
  );
}